body {
  font-family: "Amiga Topaz Unicode Rus";
  background: #000000;
  color: rgb(0, 255, 0);
  overflow-x: hidden !important;
  text-shadow: 0 0 3px rgba(255, 255, 255, 0.4),
    0 0 5px rgba(255, 255, 255, 0.3), 0 0 10px rgba(255, 255, 255, 0.2),
    0 0 15px rgba(255, 255, 255, 0.1), 0 0 20px rgba(255, 255, 255, 0.05),
    0 0 30px rgba(255, 255, 255, 0.03), 0 0 40px rgba(255, 255, 255, 0.02),
    0 0 50px rgba(255, 255, 255, 0.01);
}

body::before {
  content: "";
  position: fixed;
  inset: 0;
  z-index: -1;
  background-image: var(--backdrop-url);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.38;
  image-rendering: pixelated;
  pointer-events: none;
}

@font-face {
  font-family: "Amiga Topaz Unicode Rus";
  src: url("../public/Fonts/AmigaTopazUnicodeRus.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "amiga_topaz_unicode_rusRg";
  src: url("../public/Fonts/AmigaTopazUnicodeRus.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

