  font-family: "amiga_topaz_unicode_rusRg";
  src: url("/Fonts/AmigaTopazUnicodeRus.woff") format("woff");
  font-weight: normal;
  font-style: normal;
} */

*,
body,
html,
::-webkit-scrollbar,
a,
a:hover {
  cursor: url("../public/Pix/cursor.gif"), auto;
}

html {
  scroll-behavior: smooth !important;
}

.red {
  color: #ff0000;
  text-decoration-thickness: 3px;
}

.green {
  color: #03ee03;
  text-decoration-thickness: 3px;
}

.blue {
  color: #5da3ff;
  text-shadow: none;
  text-decoration-thickness: 3px;
}

.startup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: black;
  z-index: 10002;
}

.startup-image {
  max-width: 60%;
  max-height: 60%;
  animation: fadeInOut 3.8s ease-in-out;
  outline: #f10ce0 4px ridge;
  box-shadow: 8px -5px 222px 73px rgba(241, 12, 224, 0.75);
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.power-led {
  display: flex;
  flex-direction: row-reverse;
  gap: 8px;
  color: rgba(255, 0, 0, 0.88);
}

.led {
  width: 10px;
  height: 10px;
  background: #00ff00;
  border-radius: 50%;
  animation: blink 2s infinite ease-in-out;
  box-shadow: 0 0 10px #00ff00;
  transform: translateY(6px);
}

@keyframes blink {
  0%,
  100% {
    opacity: 0.5;
    box-shadow: 0 0 5px #00ff00;
  }
  50% {
    opacity: 1;
    box-shadow: 0 0 15px #00ff00;
  }
}

.seekbar-wrapper {
  margin-right: 19px;
}

.seekbar-wrapper,
.controls-left {
  transition: opacity 0.5s ease, filter 0.5s ease;
}

.hidden {
  opacity: 0;
  filter: blur(5px);
}

.visible {
  opacity: 1;
  filter: blur(0);
}

.controls-left button.on {
  background: #008000;
}

.controls-left button.off {
  background: #ff0000;
}

.controls-left button.playing {
  background: #ff0000;
  color: #ffffff;
}

.controls-left button.paused {
  background: #008000;
  color: black;
}

/* Fly-out panel */
.flyout-trigger {
  position: fixed;
  right: 20px;
  top: 0;
  bottom: 0;
  width: 88px;
  z-index: 10001;
  /* cursor: pointer; */
}

.flyout {
  position: fixed;
  right: 40px;
  top: 0;
  bottom: 0;
  width: 384px;
  background: rgba(36, 135, 120, 0.78);
  transform: translate(104%, 42%);
  transition: transform 0.3s ease;
  z-index: 10000;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  font-size: 20px;
  height: 44vh;
  padding: 20px;
  text-align: center;
  line-height: 1.4;
  border: 4px #33ff00 ridge;
  flex-direction: column;
}

.flyout::before {
  content: "";
  position: absolute;
  transform: translateX(-113%) translateY(22vh) rotate(180deg);
  border-left: 20px solid #33ff00;
  border-bottom: 20px solid transparent;
  border-top: 20px solid transparent;
  height: 0;
  width: 0;
}

.flyout-trigger:hover + .flyout,
.flyout:hover {
  transform: translate(0, 42%);
  right: 0;
}

.mini {
  font-size: 20px;
  color: #c70808;
  text-align: center;
}
.about {
  font-size: 20px;
  color: #004dd3;
  margin-bottom: -20px;
}

.flyout-help {
  display: flex;
  flex-direction: column;
  font-size: 17px;
  margin: 0;
  padding: 0;
  text-align: left;
}

.fly-chip,
.fly-mpt,
.fly-trk {
  text-decoration-thickness: 3px;
  transition: all 0.3s ease-in-out;
}

.fly-chip:hover,
.fly-mpt:hover,
.fly-trk:hover {
  color: #000000;
  transition: all 0.3s ease-in-out;
}

.fly-chip {
  color: #c70808;
  margin-left: 12px;
  margin-right: 12px;
}

.fly-mpt {
  color: #004dd3;

  margin-right: 12px;
}
.fly-trk {
  margin-left: 12px;
  margin-right: 12px;
  color: #00fe00;
}

.app.flex {
  display: flex;
  margin-top: 50px;
}

.sticky-controls {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  background: rgba(20, 20, 20, 0.85);
  padding: 8px 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.8);
  width: 1280px;
  margin: 0 auto;
}

.player-logo {
  color: #00ff51;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 78px;
}

.logo-text {
  text-decoration: none;
  color: #adff2f;
}

.title-container {
  text-align: center;
}

.logo-title {
  font-size: 40px;
  width: 404%;
}

.title-span {
  transform: translateY(-15px);
  display: inline-block;
}

.player-logo h1 {
  margin-left: 10px;
  width: 238px;
}
.controls-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 150px;
}
.controls-left {
  display: flex;
  gap: 4px;
  margin-right: 40px;
}

.controls-left button {
  margin-right: 8px;
  padding: 18px 18px;
  font-family: "Amiga Topaz Unicode Rus";
  font-size: 20px;
  outline: #00ff2a 4px ridge;
  width: 95px;
}

.progress-and-eq {
  display: flex;
  align-items: center;
  gap: 12px;
}
.seek-bar-wrap {
  width: 224px;
}

.volume-wrapper,
.position-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: -10px;
}

.text-4-slider {
  margin-right: 8px;
}

.seek-bar {
  width: 100%;
  height: 10px;
  -webkit-appearance: none;
  appearance: none;
  background: linear-gradient(
    90deg,
    red,
    orange,
    yellow,
    lime,
    cyan,
    blue,
    magenta
  );
  border-radius: 10px;
  outline: none;
}

.audio-bar {
  width: 100%;
  height: 10px;
  -webkit-appearance: none;
  appearance: none;
  background: linear-gradient(
    90deg,
    magenta,
    blue,
    cyan,
    lime,
    yellow,
    orange,
    red
  );
  border-radius: 10px;
  outline: none;
}

.audio-bar[type="range"]::-moz-range-thumb,
.seek-bar[type="range"]::-moz-range-thumb {
  background: linear-gradient(
    179deg,
    rgba(41, 224, 227, 1) 34%,
    rgba(245, 167, 0, 1) 100%
  );
  height: 24px;
  width: 8px;
  border-radius: 2px;
  outline: 1px #000000 solid;
  box-shadow: 0 0 8px rgba(136, 255, 215, 0.8);
}

.eq-wrapper {
  width: 128px;
}
.equalizer canvas {
  width: 84%;
  height: 40px;
  display: block;
}

.left {
  width: 200px;
  background: #052247b5;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: fadeInOut 3.8s ease-in-out;
  outline: #ffffff 4px ridge;
  box-shadow: -3px 48px 200px 73px rgba(255, 0, 0, 0.75);
  margin-top: -90px;

  @media (width <= 1280px) {
    margin-top: 0;
  }
}

.left h2 {
  text-transform: uppercase;
  letter-spacing: 2px;
}

.left ul {
  list-style: none;
  padding: 0;
}
.left li {
  padding: 16px;
  margin-bottom: 5px;
  background: #0a2f4f;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: #ffffff 4px solid;
  margin-bottom: 10px;
  transition: all 0.4s ease-in-out;
}

.left li:hover {
  background: #b50000;
  transition: all 0.4s ease-in-out;
}

.left li.active {
  background: #b50000;
  transition: all 0.4s ease-in-out;
}

.left {
  font-size: 20px;
  transition: all 0.4s ease-in-out;
}

.left-right-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  margin-top: 124px;
  flex-basis: 1200px;
  margin-left: 55px;
  @media screen and (max-width: 2000px) {
    flex-basis: 1900px;
  }
  @media screen and (max-width: 2560px) {
    flex-basis: 2550px;
  }
}

.right {
  flex: 1;
  padding: 20px;
  padding-top: 50px;
  padding-right: 50px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  font-size: 20px;
  flex-direction: column;
  animation: fadeInOut 3.8s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.right p {
  display: flex;
  justify-content: center;

  animation-name: moveUpDown;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

@keyframes moveUpDown {
  0%,
  100% {
    transform: translateY(-1px);
  }
  50% {
    transform: translateY(1px);
  }
}

.introDescription1 {
