.playlist-title-puter {
  display: flex;
  gap: 20px;
  flex-direction: row-reverse;
  justify-content: flex-end;
  letter-spacing: 3px;
  color: #00ff00;
}

.puter {
  filter: brightness(0) saturate(100%) invert(62%) sepia(33%) saturate(7493%)
    hue-rotate(87deg) brightness(120%) contrast(124%)
    drop-shadow(0 0 6px #05ba05) drop-shadow(0 0 14px #02ae02);
  animation: floating 3s linear infinite alternate;
}

@keyframes floating {
  0%,
  100% {
    transform: translate(2px, 3px);
  }
  50% {
    transform: translate(3px, -4px);
  }
}

.power-switch {
  position: fixed;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 12000;
  cursor: pointer;
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-tap-highlight-color: transparent;
  transition: transform 120ms ease, filter 120ms ease;
  user-select: none;
  outline: none;
  box-shadow: -3px 48px 200px 73px rgba(255, 0, 0, 0.75);
  transition: all 0.5s ease-in-out;
}

.power-switch:hover {
  box-shadow: -3px 48px 200px 73px rgb(0, 237, 194);
  transition: all 0.5s ease-in-out;
}

.power-switch:active,
.power-switch:focus {
  transform: translateX(-50%) translateY(2px) scale(0.99);
}
.power-switch.disabled {
  opacity: 0.6;
  pointer-events: none;
}
.power-switch-img {
  width: 100%;
  height: auto;
  display: block;
  border-radius: 6px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.6),
    inset 0 2px 6px rgba(255, 255, 255, 0.05);
}

@media (width > 1920) {
  .right {
    width: 80vw;
  }
}
